n  background: #000;\n  color: #8a8a8a;\n  font-size: 14px;\n  padding: 60px 0 20px;\n}\n\n.footer p {\n  color: #8a8a8a;\n}\n\n.footer h3 {\n  color: #ffffff;\n  margin-bottom: 20px;\n}\n\n.footer-col-1,\n.footer-col-2,\n.footer-col-3,\n.footer-col-4 {\n  min-width: 250px;\n  margin-bottom: 20px;\n}\n\n.footer-col-1 {\n  flex-basis: 30%;\n}\n\n.footer-col-2 {\n  flex: 1;\n  text-align: center;\n}\n\n.footer-col-2 img {\n  width: 180px;\n  margin-bottom: 20px;\n}\n\n.footer-col-3,\n.footer-col-4 {\n  flex-basis: 12%;\n  text-align: center;\n}\n\nul {\n  list-style-type: none;\n}\n\n.app-logo {\n  margin-top: 20px;\n}\n.app-logo img {\n  width: 140px;\n}\n\n.footer hr {\n  border: none;\n  background: #b5b5b5;\n  height: 1px;\n  margin: 20px 0;\n}\n\n.copyright {\n  text-align: center;\n}\n\n.menu-icon {\n  width: 28px;\n  margin-left: 20px;\n  display: none;\n}\n\n/* media query for menu */\n\n@media only screen and (max-width: 800px) {\n  nav ul {\n    position: absolute;\n    top: 70px;\n    left: 0;\n    background: #333;\n    width: 100%;\n    overflow: hidden;\n    transition: max-height 0.5s;\n  }\n  nav ul li {\n    display: block;\n    margin-right: 50px;\n    margin-top: 10px;\n    margin-bottom: 10px;\n  }\n\n  nav ul li a {\n    color: #fff;\n  }\n  .menu-icon {\n    display: block;\n    cursor: pointer;\n  }\n}\n\n/* all products page */\n\n.row-2 {\n  justify-content: space-between;\n  margin: 100px auto 50px;\n}\n\nselect {\n  border: 1px solid #ff523b;\n  padding: 5px;\n}\n\nselect:focus {\n  outline: none;\n}\n\n.page-btn {\n  margin: 0 auto 80px;\n}\n\n.page-btn span {\n  display: inline-block;\n  border: 1px solid #ff523b;\n  margin-left: 10px;\n  width: 40px;\n  height: 40px;\n  text-align: center;\n  line-height: 40px;\n  cursor: pointer;\n}\n\n.page-btn span:hover {\n  background: #ff523b;\n  color: #ffffff;\n}\n\n/* single product details */\n\n.single-product {\n  margin-top: 80px;\n}\n\n.single-product .col-2 img {\n  padding: 0;\n}\n\n.single-product .col-2 {\n  padding: 20px;\n}\n\n.single-product h4 {\n  margin: 20px 0;\n  font-size: 22px;\n  font-weight: bold;\n}\n\n.single-product select {\n  display: block;\n  padding: 10px;\n  margin-top: 20px;\n}\n\n.single-product input {\n  width: 50px;\n  height: 40px;\n  padding-left: 10px;\n  font-size: 20px;\n  margin-right: 10px;\n  border: 1px solid #ff523b;\n}\n\ninput:focus {\n  outline: none;\n}\n\n.single-product .fas {\n  color: #ff523b;\n  margin-left: 10px;\n}\n\n.small-img-row {\n  display: flex;\n  justify-content: space-between;\n}\n\n.small-img-col {\n  flex-basis: 24%;\n  cursor: pointer;\n}\n\n/* cart items */\n\n.cart-page {\n  margin: 90px auto;\n}\n\ntable {\n  width: 100%;\n  border-collapse: collapse;\n}\n\n.cart-info {\n  display: flex;\n  flex-wrap: wrap;\n}\n\nth {\n  text-align: left;\n  padding: 5px;\n  color: #ffffff;\n  background: #ff523b;\n  font-weight: normal;\n}\ntd {\n  padding: 10px 5px;\n}\n\ntd input {\n  width: 40px;\n  height: 30px;\n  padding: 5px;\n}\n\ntd a {\n  color: #ff523b;\n  font-size: 12px;\n}\n\ntd img {\n  width: 80px;\n  height: 80px;\n  margin-right: 10px;\n}\n\n.total-price {\n  display: flex;\n  justify-content: flex-end;\n}\n\n.total-price table {\n  border-top: 3px solid #ff523b;\n  width: 100%;\n  max-width: 400px;\n}\n\ntd:last-child {\n  text-align: right;\n}\n\nth:last-child {\n  text-align: right;\n}\n/* account page */\n.account-page {\n  padding: 50px 0;\n  background: radial-gradient(#fff, #ffd6d6);\n}\n\n.form-container {\n  background: #ffffff;\n  width: 300px;\n  height: 400px;\n  position: relative;\n  text-align: center;\n  padding: 20px 0;\n  margin: auto;\n  box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.1);\n  overflow: hidden;\n}\n\n.form-container span {\n  font-weight: bold;\n  padding: 0 10px;\n  color: #555555;\n  cursor: pointer;\n  width: 100px;\n  display: inline-block;\n}\n\n.form-btn {\n  display: inline-block;\n}\n\n.form-container form {\n  max-width: 300px;\n  padding: 0 20px;\n  position: absolute;\n  top: 130px;\n  transition: transform 1s;\n}\n\nform input {\n  width: 100%;\n  height: 30px;\n  margin: 10px 0;\n  padding: 0 10px;\n  border: 1px solid #ccc;\n}\n\nform .btn {\n  width: 100%;\n  border: none;\n  cursor: pointer;\n  margin: 10px 0;\n}\n\nform .btn:focus {\n  outline: none;\n}\n\n#LoginForm {\n  left: -300px;\n}\n\n#RegForm {\n  left: 0;\n}\n\nform a {\n  font-size: 12px;\n}\n\n#Indicator {\n  width: 100px;\n  border: none;\n  background: #ff523b;\n  height: 3px;\n  margin-top: 8px;\n  transform: translateX(100px);\n  transition: transform 1s;\n}\n\n/* media query for less than 600 screen size */\n\n@media only screen and (max-width: 600px) {\n  .row {\n    text-align: center;\n  }\n  .col-2,\n  .col-3,\n  .col-4 {\n    flex-basis: 100%;\n  }\n\n  .single-product .row {\n    text-align: left;\n  }\n\n  .single-product .col-2 {\n    padding: 20px 0;\n  }\n  .single-product h1 {\n    font-size: 26px;\n    line-height: 32px;\n  }\n  .cart-info p {\n    display: none;\n  }\n}\n"],"sourceRoot":""}]);
